.section {
  padding-top: rem(75);
  padding-bottom: rem(90);
  position: relative;
  > .container {
    position: relative;
    z-index: 1; }
  @include lg {
    padding-top: rem(65);
    padding-bottom: rem(75); }
  @include md {
    padding-top: rem(55);
    padding-bottom: rem(55); }
  @include sm {
    padding-top: rem(45);
    padding-bottom: rem(45); }
  &__title {
    text-align: center;
    margin-bottom: rem(15);
    &--side {
      max-width: 690px;
      text-align: left; } }
  &__subttl {
    text-align: center;
    margin-bottom: rem(40);
    margin-left: auto;
    margin-right: auto;
    max-width: 690px;
    &--side {
      text-align: left;
      margin-left: 0;
      margin-right: 0; } }
  &__block {
    &--lg {
      margin-bottom: rem(80); } }
  &__s-text {
    max-height: 455px; }
  &__word {
    position: absolute;
    z-index: 0;
    @include xxs {
      display: none; }
    &--bottom-left {
      bottom: rem(20);
      left: rem(30); }
    &--bottom-right {
      bottom: rem(20);
      right: 0; } }
  &__pagination {
    margin-top: rem(30); }

  &--gray {
    background-color: $light; } }
