.article {
  h1, h2, h3, h4, h5, h5 {
    @extend .title;
    @extend .title--reg;
    @extend .title--ttn; }
  h2 {
    @extend .title--h3;
    margin-bottom: rem(8); }
  h5 {
    @extend .title--h5; }
  p, ul, ol {
    @include notlast {
      margin-bottom: rem(20); } }
  a {
    &:not(.btn) {
      text-decoration: underline;
      color: $blue; } }
  ul {
    li {
      @include list-bullet;
      position: relative;
      padding-left: rem(10);
      &::before {
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0; } } } }
