.subscribe {
  max-width: 450px;
  &__title {
    margin-bottom: rem(25); }
  &__form {} }


.subscribe-form {
  display: flex;
  @include xxs {
    display: block; }
  &__input, &__btn {
    width: calc(50% - #{rem(15)});
    @include xxs {
      width: 100%; } }
  &__input {
    background-color: transparent;
    border: 0;
    border-bottom: 2px solid #ebebeb;
    height: rem(45);
    padding: .3em 0;
    font-size: em(14);
    margin-right: rem(15);
    @include placeholder {
      color: inherit; }
    @include xxs {
      margin-right: 0;
      margin-bottom: rem(15); } }
  &__btn {
    margin-left: rem(15);
    @include xxs {
      margin-left: 0; } } }
