.title {
  display: block;
  font-weight: 700;
  text-transform: uppercase;
  color: $c-title;
  line-height: 1;
  @include font-title;
  &--h1 {
    font-size: em(34); }
  &--h2 {
    font-size: em(36); }
  &--h3 {
    font-size: em(21); }
  &--h5 {
    line-height: inherit;
    font-size: em(16); }
  &--reg {
    @include reg; }
  &--semi {
    @include reg;
    font-weight: 600; }
  &--ttn {
    text-transform: none; } }

.title-img {
  flex-wrap: wrap;
  &__title {
    display: inline;
    vertical-align: middle; }
  &__img {
    width: rem(60);
    display: inline-block;
    vertical-align: middle;
    margin-left: .3em;
    @include lg {
      width: rem(50); }
    @include md {
      width: rem(40); }
    @include sm {
      width: rem(35); }
    @include xxs {
      rem(25) {} } } }

blockquote, .blockquote {
  padding: rem(25);
  position: relative;
  background-color: $white;
  @include shadow;
  @include before {
    @include gradient;
    top: 0;
    bottom: 0;
    left: 0;
    position: absolute;
    width: 4px; } }

.blockquote {
  &--plain {
    box-shadow: none;
    @include before {
      background-image: none;
      background-color: #efefef; } } }

