.footer {
  background-color: $light;
  padding-top: rem(55);
  padding-bottom: rem(55);
  @include sm {
    padding-top: rem(40);
    padding-bottom: rem(40); }
  &__dots {
    position: absolute;
    top: .4rem;
    left: 0;
    opacity: .4;
    width: rem(60);
    @include lg {
      display: none; } }
  &__blocks {
    position: relative;
    padding-left: percentage(200 / 1410);
    display: flex;
    flex-wrap: wrap;
    @include lg {
      padding-left: 0; } }
  &__block {
    width: 33.33333%;
    padding-right: rem(15);
    @include xs {
      width: 50%;
      @include notlast {
        margin-bottom: rem(30); } } }
  &__title {
    margin-bottom: rem(20); }

  &__copy {
    text-align: center;
    font-size: em(12);
    margin-top: rem(30); } }

