.teacher {
  @include shadow;
  background-color: $white;
  border-radius: $radius-sm;
  padding: rem(35);
  display: flex;
  align-items: center;
  height: 100%;
  @include lg {
    padding: rem(25); }
  @include xs {
    display: block; }
  &__left, &__right {
    width: 50%;
    @include xs {
      width: 100%;
      padding-left: 0 !important;
      padding-right: 0 !important; } }
  &__left {
    padding-right: $grid-gap / 2;
    padding-right: $half-gap;
    text-align: center;
    @include xs {
      margin-bottom: rem(15); } }
  &__right {
    padding-left: $grid-gap / 2;
    padding-left: $half-gap; }
  &__img {
    @include cover;
    background-color: $c-text;
    border-radius: 50%;
    margin-bottom: rem(15);
    height: rem(210);
    width: rem(210);
    margin-left: auto;
    margin-right: auto;
    @include md {
      width: rem(180);
      height: rem(180); } } }

