.s-contacts {
  $this: &;
  position: relative;
  padding-top: rem(100);
  padding-bottom: rem(100);
  @include md {
    padding-top: rem(55);
    padding-bottom: 0; }
  @include sm {
    padding-top: rem(45); }
  &__map {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    iframe {
      height: 100%;
      width: 100%;
      left: 0;
      top: 0;
      position: absolute; }
    @include md {
      position: relative;
      padding-top: 50%; } }
  &__block {
    position: relative;
    z-index: 1;
    max-width: 450px;
    @include md {
      max-width: none;
      margin-bottom: rem(30); } }

  &--empty {
    height: 100vh;
    #{$this}__map {
      @include md {
        position: absolute; } } } }
